.post{
    opacity: 0;
    visibility: hidden;

    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.5);

    
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.active{
    opacity: 1;
    visibility: visible;

}
.content{
    background-color: aliceblue;
    border: 1px solid #000;
    border-radius: 20px;
    width: 800px;
    padding: 50px 100px;
}