*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App{
  max-width: 1100px;
  padding: 100px 30px 0 30px;
  margin: 0 auto;
}
.post{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 10px auto 10px auto;
  padding: 15px 15px;
  border: 2px #000 solid;
  border-radius: 20px;
}



.post__content strong{
  font-size: 20px;
  font-family: Arial, Helvetica, sans-serif;
  line-height: 30px;
}
.post__content p{  
  font-size: 14px;
  font-family: Arial, Helvetica, sans-serif;
  line-height: 10px;
}

.postnan{
  text-align: center;
  font-size: 30px;
  font-family: Arial, Helvetica, sans-serif;
  line-height: 20px;
}

.post-enter {
  transform: translateX(-1500px);
}
.post-enter-active {
  transform: translateX(1500px);
  transition: all 800ms ease-in;
}
.post-exit {
  opacity: 1;
}
.post-exit-active {
  transform: translateX(-1500px );
  transition: all 800ms ease;
}

.pagination__wrapper{
  display: flex;
  margin: 0 auto;
  justify-content: space-around;
  max-width: 400px;

  padding: 20px 0 50px 0;
}
.pagination{
  color: rgb(51, 35, 141);
  cursor: pointer;
}


.pagination__current{
  margin: -6px -7px 0 0;
  color:aliceblue;
  background: rgb(51, 35, 141);
  padding: 6px 7px;
  cursor: pointer;
  border-radius: 6px;
}

.navBar{
  height: 50px;
  width: 100vw;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 0 15px;

  background-color: aqua;
}

.navBar__item{
  display: flex;
  flex-direction: column;
  justify-content: end;
}

.buttons{
  display: flex;
  flex-wrap: nowrap;
}

@media(max-width:700px){
  .buttons{
    display: flex;
    flex-wrap: wrap;
  }
}


